@use '../../assets/styles/variables' as vars;

.navbar {
  position: relative;
}

.navbar__links {
  display: flex;
  gap: 16px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.navbar__item {
  position: relative;
}

.navbar__link {
  display: block;
  padding: 1rem;
  color: vars.$white;
  text-decoration: none;

  &:hover {
    background-color: vars.$yellow;
  }
}

.navbar__dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: vars.$darkblue;
  min-width: 200px;
  z-index: 1000;
}

.navbar__dropdown-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.navbar__dropdown-item {
  display: block;
  padding: 1rem;
  white-space: nowrap;
  text-decoration: none;
  color: inherit;

  &:hover {
    background-color: vars.$yellow;
  }
}
